/* Estilos para el fondo */
.login-background {
  background-image: url('/public/ueprios.jpg'); /* Asegúrate de que esté en /public */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.login-background::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(5px); /* Aplica el desenfoque */
  background-color: rgba(42, 41, 41, 0.382); /* Capa para mayor contraste */
  z-index: 0;
}

.login-background > * {
  position: relative;
  z-index: 1;
}

/* Estilo del contenedor Flex */
.login-flex {
  height: 100vh;
  justify-content: center;
  align-items: center;
  padding: 0 20px; /* Asegura que haya espacio en los bordes en pantallas pequeñas */
}

/* Estilos para el Card */
.login-card {
  width: 400px;
  background-color: rgba(0, 0, 0, 0.2); /* Fondo con transparencia */
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5); /* Sombra suave */
  backdrop-filter: blur(2px); /* Difumina el fondo */
  padding: 24px; /* Padding interno para no estar pegado a los bordes */
}

/* Estilo para el logo */
.logo-container {
  display: flex;
  justify-content: center;
  margin-bottom: 24px;
}

.logo {
  width: 160px;
  max-width: 100%;
}

/* Estilos para los labels */
.ant-form-item-label {
  color: white;
  font-weight: bold;
  font-size: 20px; /* Tamaño del texto del label */
}

/* Estilo de los inputs */
.ant-input, .ant-input-password {
  font-size: 1.25rem; /* Aumento del tamaño del texto dentro de los inputs */
  padding: 12px; /* Aumento del tamaño del padding */
}

/* Estilo del botón */
.ant-btn {
  font-size: 1.25rem; /* Tamaño de la fuente del botón */
  font-weight: bold;  /* Texto en negrita */
  padding: 16px; /* Ajustado para mejor visualización */
  margin-top: 15px;
}

/* Media query para pantallas pequeñas (dispositivos móviles) */
@media (max-width: 768px) {
  .login-background {
    background-size: cover; /* Cambiar a cover para que la imagen se amplíe */
    background-position: center; /* Asegura que el centro de la imagen esté visible */
  }

  .login-flex {
    padding: 10px; /* Ajusta los márgenes para móviles */
    justify-content: flex-start;
    height: auto; /* No debe ocupar toda la altura en móviles */
  }

  .login-card {
    width: 100%; /* Asegura que el card ocupe todo el ancho disponible */
    max-width: 350px; /* Evita que sea demasiado grande */
  }

  .ant-form-item-label {
    font-size: 1rem; /* Ajusta el tamaño de los labels */
  }

  .ant-form-item {
    font-size: 1rem; /* Ajuste en el tamaño del contenido */
  }

  .ant-input, .ant-input-password {
    font-size: 1.125rem; /* Ajuste en el tamaño de los inputs */
    padding: 10px; /* Menos padding en inputs */
  }

  .ant-btn {
    font-size: 1rem; /* Ajuste en el tamaño del botón */
    padding: 16px; /* Ajuste del padding del botón */
  }
}

@media (max-width: 480px) {
  /* Ajustes adicionales para pantallas muy pequeñas */
  .login-card {
    width: 100%;
    max-width: 300px; /* Asegura que en pantallas pequeñas se vea adecuado */
    padding: 16px; /* Un poco más de espacio interno */
  }

  .ant-btn {
    font-size: 1rem; /* Mejor para botones pequeños */
    padding: 12px;
  }

  .ant-input, .ant-input-password {
    font-size: 1rem; /* Ajuste en el tamaño de los inputs */
  }
}
